<template>
<div>
  <div class="goods">
    <!--左侧menu-->
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li v-for="(item,index) in goods" class="menu-item" :class ="{'current':currentIndex===index}" @click="selectMenu(index,$event)">
          <span class="text">
            <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
          </span>
        </li>
      </ul>
    </div>

    <!--商品列表-->
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="item in goods" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li @click="selectFood(food,$event)" v-for="food in item.foods" class="food-item">
              <div class="icon"> <!--商品图片-->
                <img width="57" height="57" :src="food.icon">
              </div>
              <div class="content">
                <h2 class="name">{{food.name}}</h2>
                <p class="desc">{{food.description}}</p>
                <div class="extra">
                  <span class="count">月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
                </div>
                <div class="price">
                  <span class="now">¥{{food.price}}</span><span class="old" v-show="food.oldPrice">¥{{food.oldPrice}}</span>  <!--原价-->
                </div>
                <div class="cartcontrol-wrapper">
                  <cartcontrol @add="addFood" :food="food"></cartcontrol>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!--购物车（底部）-->
    <shopcart ref="shopcart" :select-foods="selectFoods" :delivery-price="seller.deliveryPrice" :min-peice="seller.minPrice"></shopcart>
  </div>
  <food  @add="addFood" :food="selectedFood" ref="food"></food>
</div>
</template>
<script type="text/ecmascript-6">
  import BScroll from 'better-scroll';//引入better-scroll库
  import shopcart from 'components/shopcart/shopcart';
  import cartcontrol from 'components/cartcontrol/cartcontrol';
  import  food from 'components/food/food';

  const ERR_OK = 0;

  export default {
    props: {
      seller: {
        type: Object
      }
    },
    data(){
      return{
        goods:[],
        listHeight:[],
        scrollY: 0,
        selectedFood:{}
      }
    },
    computed:{
      currentIndex(){
        for (let i = 0; i<this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)){
            return i;
          }
        }
        return 0;
      },
      selectFoods(){    //与右侧加减数量联动（联动cartcontrol组件）
        let foods = [];
        this.goods.forEach((good) => {
          good.foods.forEach((food) => {
            if (food.count){
                foods.push(food);
            }
          })
        })
        return foods;
      }
    },
    created() {
      //对应活动的图标
      this.classMap = ['decrease','discount','special','invoice','guarantee'];

      this.$http.get('/api/goods').then((response)=>{
        response = response.body;
        if (response.errno === ERR_OK){
          this.goods = response.data;
          this.$nextTick(() => {
            this._initScroll(); //上下滚动
            this._calculateHeight();  //计算高度（滚动时定位）
          });
        }
      });
    },
    methods: {
      selectMenu(index, event){   //做菜单点击事件
        if (!event._constructed){  //判断区分PC端（_constructed 只在移动端有此属性防止PC端重复调用）
          return  ;
        }

        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
        let el =foodList[index];
        this.foodsScroll.scrollToElement(el,300); //点击左菜单，菜单列表滚动到对应位置
      },
      selectFood(food,event){   //弹出菜品详情
        if (!event._constructed){  //判断区分PC端（_constructed 只在移动端有此属性防止PC端重复调用）
          return  ;
        }
        this.selectedFood = food;
        this.$refs.food.show();
      },
      addFood(target){
        this._drop(target)
      },
      _drop(target){
        //体验优化，异步执行动画
        this.$nextTick(() => {
          this.$refs.shopcart.drop(target);
        });

      },
      _initScroll(){
        this.meunScroll = new BScroll(this.$refs.menuWrapper,{
            click: true
        });

        this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{
          probeType: 3, //监听滚动的实时位置（具体可查看AIP）
          click: true
        });
        this.foodsScroll.on('scroll',(pos) => {
          this.scrollY = Math.abs(Math.round(pos.y));
        })
      },
      _calculateHeight(){   //抓取每一节的高度
        let foodList = this.$refs.foodsWrapper.getElementsByClassName('food-list-hook');
        let height = 0;
        this.listHeight.push(height);
        for (let i = 0; i< foodList.length; i++){
            let item = foodList[i];
            height += item.clientHeight;
            this.listHeight.push(height);
        }
      }
    },
    components:{
        'shopcart':shopcart,
        'cartcontrol':cartcontrol,
        'food': food
    },
//    events: {  //购物车小球动画方法
//        'add'(target){
//            this._drop(target)
//        }
//    }
  };
</script>
<style type="styles" rel="stylesheet/less" lang="less">
  @import "../../common/stylus/mixin";

  .goods{
    display: flex;
    position: absolute;
    top:174px;
    bottom: 46px;
    width: 100%;
    overflow: hidden;
    .menu-wrapper{
      flex: 0 0 80px;  /*flex左右布局*/
      width: 80px;
      background: #f3f5f7;
      .menu-item{
        display: table;
        height: 54px;
        width: 56px;
        line-height: 14px;
        padding: 0 12px;
        &.current{
          position: relative;
          z-index: 10;
          margin-top: -1px;
          background: #fff;
          font-weight: 700;
          .text{
            .border-none();
          }
        }
        .icon{
          display: inline-block;
          vertical-align: top;
          margin-right:2px;
          width: 12px;
          height:12px;
          font-size: 10px;
          line-height: 12px;
          background-size: 12px;
          background-repeat: no-repeat;
          &.decrease{
            .bg-image('decrease_3')
          }
          &.discount{
            .bg-image('discount_3')
          }
          &.guarantee{
            .bg-image('guarantee_3')
          }
          &.invoice{
            .bg-image('invoice_3')
          }
          &.special{
            .bg-image('special_3')
          }
        }
        .text{
          display: table-cell;
          width: 56px;
          vertical-align: middle;
          .border-px(rgba(7,17,27,0.1));
          font-size: 12px;
        }
      }
    }
    .foods-wrapper{
      flex:1;
      .title{
        padding-left: 14px;
        height: 26px;
        line-height: 26px;
        border-left:2px solid #d9dde1;
        font-size: 12px;
        color: rgb(147,153,159);
        background: #f3f5f7;
      }
      .food-item{
        display: flex;
        margin: 18px;
        padding-bottom: 18px;
        .border-px(rgba(7,17,27,0.1));
        &:last-child{
          .border-none();
          margin-bottom: 0;
        }
        .icon{
          flex: 0 0 57px;
          margin-right: 10px;
        }
        .content{
          flex: 1;
          .name{
            margin: 2px 0 8px 0;
            height:14px;
            line-height: 14px;
            font-size: 14px;
            color: rgb(7,17,27);
          }
          .desc,.extra{
            line-height: 10px;
            font-size: 10px;
            color: rgb(147,153,159);
          }
          .desc{
            line-height: 12px;
            margin-bottom: 8px;
          }
          .extra{
            .count{
              margin-right: 12px;
            }
          }
          .price{
            font-weight: 700;
            line-height: 24px;
            .now{
              margin-right: 8px;
              font-size: 14px;
              color: rgb(240,20,20);
            }
            .old{
              text-decoration: line-through;
              font-size: 14px;
              color: rgb(147,153,159);
            }
          }
          .cartcontrol-wrapper{
            position: absolute;
            right: 0;
            bottom:12px;
          }
        }
      }
    }
  }

</style>



