<template>
<div class="header">
  <div class="content-wrapper">
    <div class="avatar">
      <img width="64" height="64" :src="seller.avatar" >
    </div>
    <div class="content">
      <div class="title">
        <span class="brand"></span>
        <span class="name">{{seller.name}}</span>
      </div>
      <div class="description">
      {{seller.description}}/{{seller.deliveryTime}}分钟送达
      </div>
      <div v-if="seller.supports" class="support">
        <span class="icon" :class="classMap[seller.supports[0].type]"></span>
        <span class="text">{{seller.supports[0].description}}</span>
      </div>
    </div>
    <div v-if="seller.supports" @click="showDetail" class="support-count">
      <span class="count">{{seller.supports.length}}个</span>
      <i class="icon-keyboard_arrow_right"></i>
    </div>
  </div>
  <div class="bulletin-wrapper" @click="showDetail">
    <span class="bulletin-title"></span><span class="bulletin-text">{{seller.bulletin}}</span>
    <i class="icon-keyboard_arrow_right"></i>
  </div>
  <div class="background">
    <img :src="seller.avatar" width="100%" height="100%">
  </div>
  <transition name="detailFade">    <!--显示隐藏的动效-->
    <div v-show="detailShow" class="detail">
      <div class="detail-wrapper clearfix">
        <div class="detail-main">
          <h1 class="name">{{seller.name}}</h1>
          <div class="star-wrapper">  <!--传两个参数 size:尺寸  score：评分（数据）-->
            <star :size="48" :score="seller.score"></star>
          </div>
          <div class="title">
            <div class="line"></div>
            <div class="text">优惠信息</div>
            <div class="line"></div>
          </div>
          <ul v-if="seller.supports" class="supports">
            <!--活动数量根据索引值取对应的图标-->
            <li class="supports-item" v-for="(item,index) in seller.supports">
              <span class="icon" :class="classMap[seller.supports[index].type]"></span>
              <span class="text">{{seller.supports[index].description}}</span>
            </li>
          </ul>
          <div class="title">
            <div class="line"></div>
            <div class="text">商家公告</div>
            <div class="line"></div>
          </div>
          <div class="bulletin">
            <p class="content">{{seller.bulletin}}</p>
          </div>
        </div>
      </div>
      <!--关闭浮层按钮-->
      <div class="detail-close" @click="hideDetail"><i class="icon-close"></i></div>
    </div>
  </transition>

</div>
</template>

<script type="text/ecmascript-6">
  import star from 'components/star/star';

export default {
  props: {
    seller:{
      type:Object
    }
  },
  data(){
    return{
        detailShow: false
    }
  },
  methods: {
    showDetail(){
          this.detailShow = true;
    },
    hideDetail(){
      this.detailShow = false;
    }
  },
  created(){  //对应活动的图标
      this.classMap = ['decrease','discount','special','invoice','guarantee'];
  },
  components:{
      'star':star
  }
};
</script>

 <style type="styles" rel="stylesheet/less" lang="less">
   @import "../../common/stylus/mixin.less";
  .header{
    position: relative;
    overflow: hidden;
    color:#fff;
    background: rgba(7,17,27,0.5);
    .content-wrapper{
      position: relative;
      padding:24px 12px 18px 24px;
      font-size: 0;
      .avatar{
        display:inline-block;
        vertical-align: top;
        img{
          -webkit-border-radius:2px;
          -moz-border-radius:2px;
          border-radius:2px;
        }
      }
      .content{
        display:inline-block;
        margin-left:16px;

        .title{
          margin:2px 0 8px 0;
          .brand{
            display: inline-block;
            vertical-align: top;
            width: 30px;
            height: 18px;
            .bg-image('brand');
            background-size: 30px 18px;
            background-repeat: no-repeat;
          }
          .name{
            margin-left: 6px;
            line-height: 18px;
            font-size: 16px;
            font-weight: bold;

          }
        }
        .description{
          font-size: 12px;
          line-height: 12px;
          margin-bottom: 10px;
        }
        .support{

          .icon{
            display: inline-block;
            vertical-align: top;
            margin-right:4px;
            width: 12px;
            height:12px;
            font-size: 10px;
            line-height: 12px;
            background-size: 12px;
            background-repeat: no-repeat;
            &.decrease{
              .bg-image('decrease_1')
            }
            &.discount{
              .bg-image('discount_1')
            }
            &.guarantee{
              .bg-image('guarantee_1')
            }
            &.invoice{
              .bg-image('invoice_1')
            }
            &.special{
              .bg-image('special_1')
            }
          }
          .text{
            line-height: 12px;
            font-size: 12px;
          }
        }
      }
      .support-count{
        position: absolute;
        right: 12px;
        bottom: 18px;
        padding:0 8px;
        height: 24px;
        line-height: 24px;
        border-radius:14px;
        text-align: center;
        background: rgba(0,0,0,0.2);
        .count{
          vertical-align: top;
          font-size: 10px;
        }
        .icon-keyboard_arrow_right{
          margin-left: 2px;
          font-size: 10px;
          line-height: 24px;
        }
      }
    }
    .bulletin-wrapper{
      position: relative;
      height: 28px;
      line-height: 28px;
      padding: 0 22px 0 12px;
      white-space:nowrap;
      overflow: hidden;
      -ms-text-overflow: ellipsis;
      text-overflow: ellipsis;
      background: rgba(7,17,27,0.2);
      .bulletin-title{
        display: inline-block;
        vertical-align:top;
        margin-top: 7px;
        width: 22px;
        height: 12px;
        .bg-image('bulletin');
        background-size: 22px 12px;
        background-repeat:no-repeat;
      }
      .bulletin-text{
        vertical-align: top;
        font-size: 10px;
        margin: 0 4px;
      }
      .icon-keyboard_arrow_right{
        position:absolute;
        right: 12px;
        top:8px;
        font-size: 10px;
      }
    }
    .background{
      position: absolute;
      top:0;
      left:0;
      width: 100%;
      height:100%;
      z-index:-1;
      filter: blur(10px);
      -webkit-filter: blur(10px);
    }
    /*header 弹出层fade显示隐藏*/
    .detailFade-enter-active, .detailFade-leave-active{
      transition: opacity .5s;
    }
    .detailFade-enter, .detailFade-leave-active{
      opacity: 0;
    }
    .detail{
      position:fixed;
      top: 0;
      left: 0;
      z-index: 100;
      width: 100%;
      height: 100%;
      overflow: auto;
      background: rgba(7,17,27,0.8);
      backdrop-filter: blur(10px);
      .detail-wrapper{
        width: 100%;
        min-height: 100%;
        .detail-main{
          margin-top: 64px;
          padding-bottom: 64px;
          .name{
            line-height: 16px;
            text-align: center;
            font-size: 16px;
            font-weight: 700;
          }
          .star-wrapper{
            margin-top: 18px;
            padding: 2px 0;
            text-align: center;
          }
          .title{
            display: flex;
            width: 80%;
            margin: 28px auto 24px auto;
            .line{
              flex: 1;
              position: relative;
              top:-6px;
              border-bottom: 1px solid rgba(255,255,255,0.2);

            }
            .text{
              padding: 0 12px;
              font-size: 14px;
              font-weight: 700;
            }
          }
          .supports{
            width: 80%;
            margin: 0 auto;
            .supports-item{
              padding: 0 12px;
              margin-bottom: 12px;
              font-size: 0;
              &:last-child{
                margin-bottom: 0;
              }
              .icon{
                display: inline-block;
                width: 16px;
                height: 16px;
                vertical-align: top;
                margin-right: 6px;
                background-size: 16px 16px;
                background-repeat: no-repeat;
                &.decrease{
                  .bg-image('decrease_2')
                }
                &.discount{
                  .bg-image('discount_2')
                }
                &.guarantee{
                  .bg-image('guarantee_2')
                }
                &.invoice{
                  .bg-image('invoice_2')
                }
                &.special{
                  .bg-image('special_2')
                }
              }
              .text{
                line-height: 16px;
                font-size: 12px;
              }
            }
          }
          .bulletin{
            width: 80%;
            margin: 0 auto;
            .content{
              padding: 0 12px;
              line-height: 24px;
              font-size: 12px;

            }
          }
        }
      }
      .detail-close{
        position: relative;
        width: 32px;
        height:32px;
        margin: -64px auto 0 auto;
        font-size: 32px;
      }
    }

  }
</style>



